  .header {
    top: 0;
    left: 0;
    width: 100%;
    height: 70px;
    position: fixed;
    box-sizing: border-box;
    background-color: transparent;
  }
  .header.colorChange{
    background-color: rgba(246, 246, 246, 0.8);
  }
  .menu {
    background-color: transparent;
    border-radius: 5px;
    margin: 15px 0 0 10px;
    cursor: pointer;
    display: flex;
    border: none;
    padding: 0;
  }
  .line {
    fill: none;
    stroke: #212A3E;
    stroke-width: 6;
    transition: stroke-dasharray 600ms cubic-bezier(0.4, 0, 0.2, 1),
      stroke-dashoffset 600ms cubic-bezier(0.4, 0, 0.2, 1);
  }
  .line1 {
    stroke-dasharray: 60 207;
    stroke-width: 6;
  }
  .line2 {
    stroke-dasharray: 60 60;
    stroke-width: 6;
  }
  .line3 {
    stroke-dasharray: 60 207;
    stroke-width: 6;
  }
  .opened .line1 {
    stroke-dasharray: 90 207;
    stroke-dashoffset: -134;
    stroke-width: 6;
  }
  .opened .line2 {
    stroke-dasharray: 1 60;
    stroke-dashoffset: -30;
    stroke-width: 6;
  }
  .opened .line3 {
    stroke-dasharray: 90 207;
    stroke-dashoffset: -134;
    stroke-width: 6;
  }
 .opened {
 stroke-dasharray: 85 150;
 stroke-dashoffset: -135;
 transition: all 0.5s ease-in-out;
 }
  
  img {
    width: 40px;
    height: 40px;
    float: right;
    margin-top: -40px;
    margin-right: 10px;
    border-radius: 20px;
  }
  .hidden {
    display: none;
  }
  nav {
    width: 100%;
    margin-top: 15px;
    background-color: #F6F6F6;
    animation-name: expanded;
    animation-duration: 0.5s;
  }
  
  @keyframes expanded {
    from {
      opacity: 0;
      transform: translateY(-5px);
    }
    to {
      opacity: 1;
      transform: translateY(0);
    }
  }
  nav ul {
    list-style: none;
    margin-left: 0;
  }
  
  nav ul li {
    padding-left: 10px;
    border-bottom: 1px solid #f0f0f0;
  }
  nav ul li a {
    color: #212A3E;
    line-height: 3;
    font-weight: bold;
    letter-spacing: 1px;
    text-decoration: none;
  }
  