@import url(https://fonts.googleapis.com/css2?family=Ysabeau&display=swap);
* {
  margin: 0;
  padding: 0;
}
body {
  margin: 0;
  padding: 0;
  font-size: 16px;
  color: #212A3E;
  background-color: #F6F6F6;
  font-family: -apple-system, BlinkMacSystemFont, 'Ysabeau', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



/* @media (max-width: 480px) {
   */
  .header {
    top: 0;
    left: 0;
    width: 100%;
    height: 70px;
    position: fixed;
    box-sizing: border-box;
    background-color: transparent;
  }
  .header.colorChange{
    background-color: rgba(246, 246, 246, 0.8);
  }
  .menu {
    background-color: transparent;
    border-radius: 5px;
    margin: 15px 0 0 10px;
    cursor: pointer;
    display: flex;
    border: none;
    padding: 0;
  }
  .line {
    fill: none;
    stroke: #212A3E;
    stroke-width: 6;
    transition: stroke-dasharray 600ms cubic-bezier(0.4, 0, 0.2, 1),
      stroke-dashoffset 600ms cubic-bezier(0.4, 0, 0.2, 1);
  }
  .line1 {
    stroke-dasharray: 60 207;
    stroke-width: 6;
  }
  .line2 {
    stroke-dasharray: 60 60;
    stroke-width: 6;
  }
  .line3 {
    stroke-dasharray: 60 207;
    stroke-width: 6;
  }
  .opened .line1 {
    stroke-dasharray: 90 207;
    stroke-dashoffset: -134;
    stroke-width: 6;
  }
  .opened .line2 {
    stroke-dasharray: 1 60;
    stroke-dashoffset: -30;
    stroke-width: 6;
  }
  .opened .line3 {
    stroke-dasharray: 90 207;
    stroke-dashoffset: -134;
    stroke-width: 6;
  }
 .opened {
 stroke-dasharray: 85 150;
 stroke-dashoffset: -135;
 transition: all 0.5s ease-in-out;
 }
  
  img {
    width: 40px;
    height: 40px;
    float: right;
    margin-top: -40px;
    margin-right: 10px;
    border-radius: 20px;
  }
  .hidden {
    display: none;
  }
  nav {
    width: 100%;
    margin-top: 15px;
    background-color: #F6F6F6;
    -webkit-animation-name: expanded;
            animation-name: expanded;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
  }
  
  @-webkit-keyframes expanded {
    from {
      opacity: 0;
      -webkit-transform: translateY(-5px);
              transform: translateY(-5px);
    }
    to {
      opacity: 1;
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
  }
  
  @keyframes expanded {
    from {
      opacity: 0;
      -webkit-transform: translateY(-5px);
              transform: translateY(-5px);
    }
    to {
      opacity: 1;
      -webkit-transform: translateY(0);
              transform: translateY(0);
    }
  }
  nav ul {
    list-style: none;
    margin-left: 0;
  }
  
  nav ul li {
    padding-left: 10px;
    border-bottom: 1px solid #f0f0f0;
  }
  nav ul li a {
    color: #212A3E;
    line-height: 3;
    font-weight: bold;
    letter-spacing: 1px;
    text-decoration: none;
  }
  
section {
  width: 100%;
  display: grid;
  justify-content: center;
}
.polaroid{
  width: 100%;
}

.polaroid img {
  width: 400px;
  height: 300px;
  cursor: pointer;
  margin-top: 50px;
  border-radius: 5px;
  margin-bottom: 20px;
  box-shadow: 
  0 2.8px 2.2px rgba(0, 0, 0, 0.034),
  0 6.7px 5.3px rgba(0, 0, 0, 0.048),
  0 12.5px 10px rgba(0, 0, 0, 0.06),
  0 22.3px 17.9px rgba(0, 0, 0, 0.072),
  0 41.8px 33.4px rgba(0, 0, 0, 0.086),
  0 100px 80px rgba(0, 0, 0, 0.12)
  ;
}

@media screen and (max-width: 768px){
  .polaroid {
    margin-left: 2.5px;
  }
}

.pcontent {
  width: 100%;
  margin-bottom: 20px;
  text-align: center;
}
.pcontent a {
  color: #526D82;
  text-decoration: none;
}
.kotak {
  width: 100%;
  margin: 70px auto 20px auto;
  padding: 15px;
  border: 1px #212A3E;
  box-sizing: border-box;
  box-shadow: 0 1px 1.5px #9BA4B5;
}

.kotak h2:nth-child(3) {
  color: #9BA4B5;
  margin-bottom: 20px;
}

.contain {
  width: 90%;
  box-sizing: border-box;
  margin: 10px 10px 20px 10px;
}
.contain h2 {
  text-align: center;
  margin-bottom: 20px;
}

.card {
  margin-top: 10px;
}
#footer {
  margin-bottom: 20px;
}

#footer p {
  font-size: 14px;
  font-weight: bold;
  text-align: center;
}
#footer p a {
  color: #0518808a;
  text-decoration: none;
}
