.kotak {
  width: 100%;
  margin: 70px auto 20px auto;
  padding: 15px;
  border: 1px #212A3E;
  box-sizing: border-box;
  box-shadow: 0 1px 1.5px #9BA4B5;
}

.kotak h2:nth-child(3) {
  color: #9BA4B5;
  margin-bottom: 20px;
}

.contain {
  width: 90%;
  box-sizing: border-box;
  margin: 10px 10px 20px 10px;
}
.contain h2 {
  text-align: center;
  margin-bottom: 20px;
}

.card {
  margin-top: 10px;
}